</template>

<script>
	export default {
		name: "Administration",
		components: {},
		data() {
			return {
        message: '',
        searchQuery: '',
        current_post:{},
        post_view:{},
        departements:[],
        filieres:[],
        responsables:[],
        modules:[],
        enseignants:[],
        nom : '',
        description : '',
        responsable : '',
        departement : '',
        filiere : '',
        anneeScolaire : '',
        semester : '',
        elements : [
          {
            nom:'',
            enseignant:'',
            coefficient:'',
            nbrHeure:''
          },
          {
            nom:'',
            enseignant:'',
            coefficient:'',
            nbrHeure:''
          }
        ],
      }
		},
    methods: {
      toggleInsert(){
        $('#insertModal').modal('show')
      },
      toggleUpdate(post) {
        this.current_post = JSON.parse(JSON.stringify(post));
        $('#updateModal').modal('show')
      },
      toggleView(post) {
        this.post_view = JSON.parse(JSON.stringify(post));
        $('#viewModal').modal('show')
      },

      getDerpartement(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_DEPARTEMENTS ).then( data => {
          this.departements = data;
        }, () => {})
      },

      getFilieres(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_FILIERES ).then( data => {
          this.filieres = data;
        }, () => {})
      },
      getResponsable(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_RESPONSABLES_MODULE ).then( data => {
          this.responsables = data;
        }, () => {})
      },
      getEnseignants(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_ENSIEGNANTS_ELEMENT ).then( data => {
          this.enseignants = data;
        }, () => {})
      },
      getModules(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_MODULE ).then( data => {
          this.modules = data;
        }, () => {})
      },

      ajouter() {
        this.$SHARED.dispatchAsyncPost(this.$SHARED.services.AJOUTER_MODULE,
            {
              nom : this.nom,
              description : this.description,
              responsable : this.responsable,
              departement : this.departement,
              filiere : this.filiere,
              anneeScolaire : this.anneeScolaire,
              semester : this.semester,
              elements : this.elements
            }).then( data => {
              this.message = data.message;
        }, () => {})
      },
      modifier(current_filiere){
        this.$SHARED.dispatchAsyncPut(this.$SHARED.services.MODIFIER_MODULE,
            {
              _id: current_filiere._id,
              nom : current_filiere.nom,
              description : current_filiere.description,
              responsable : current_filiere.responsable,
              departement : current_filiere.departement,
              filiere : current_filiere.filiere,
              anneeScolaire : current_filiere.anneeScolaire,
              semester : current_filiere.semester,
              elements : current_filiere.elements
            }).then( data => {
          this.message = data.message;
        }, () => {})
      },
      supprimer(post){
        this.$SHARED.dispatchAsyncDelete(this.$SHARED.services.SUPPRIMER_MODULE,
            {id : post._id}).then(data => {
          this.message = data.message
        }, () => {})
      },
    },
    computed: {
      resultQuery()
      {
        if (this.searchQuery) {
          return this.modules.filter((item) => {
            return this.searchQuery.toLowerCase().split(' ').every(v => item.nom.toLowerCase().includes(v))
          })
        } else {
          return this.modules;
        }
      }
    },
    mounted() {
      this.getDerpartement();
      this.getModules();
      this.getFilieres();
      this.getEnseignants();
      this.getResponsable();

		  var min = new Date().getFullYear(),
          max = min + 5,
          select = document.getElementById('anneeScolaireInsert');
      for (var i = min; i<=max; i++){
        var opt = document.createElement('option');
        opt.innerHTML = i;
        select.appendChild(opt);
      }

      var min = new Date().getFullYear(),
          max = min + 5,
          select = document.getElementById('anneeScolaireUpdate');
      for (var i = min; i<=max; i++){
        var opt = document.createElement('option');
        opt.innerHTML = i;
        select.appendChild(opt);
      }
    }
	}
</script>

